import { Component, AfterViewInit, ElementRef, ViewChild, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController, Platform  } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
@Component({
  selector: 'app-approve-driver',
  templateUrl: './approve-driver.page.html',
  styleUrls: ['./approve-driver.page.scss'],
})
export class ApproveDriverPage implements OnInit {

  
  id: any;
  uid: any;
  number: any;
  type: any;
  name: any;
  caccs: any =[];
  daccs: any =[];

  constructor(
     private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private platform: Platform,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService) { }

  ngOnInit() {
  }
  ionViewWillEnter(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_accs',
      }
      this.accessProviders.postData(body, 'get_orders.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.result);
           console.log(res.result2);
          for(let datas of res.result){ 
            this.caccs.push(datas);
          }  
          for(let datas of res.result2){ 
            this.daccs.push(datas);
          }  
        } else {
          this.presentToast('Failed'); 
        }
      });   
    });
  }

  delete(i){
    this.id=i;
    this.alertController
      .create({
        header: "Delete",
        message: "Are you sure you want to delete?",
        buttons: [
          {
            text: "Yes",
            handler: ()=>{
              this.deluser();
            }
          },
          { text: "No" }  
        ]
      })
      .then(alertEL => alertEL.present());
  }

  deluser(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'delete_user',
        id: this.id,
        
      }
      this.accessProviders.postData(body, 'delete_product.php').subscribe((res:any) => {
        if(res.success == true) {
           console.log(res.msg);
           this.router.navigate(['/panel-admin']);
        } else {
        }
      });   
    });
  }
  
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'bottom'
    });
    toast.present();
  }  

  edit(i){
    this.uid=i
    console.log(this.uid);
    
    this.navParamService.setNavData(i);     
     this.router.navigate(['/edit-account']);
  }

}
